* {
  margin: 0 auto;
  padding: 0;
  border: none;
  outline: none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
*::-webkit-scrollbar {
  width: 6px;
  cursor: pointer !important;
}
*::-webkit-scrollbar-track {
  background: #d6dee6;
  border-radius: 6px;
}
*::-webkit-scrollbar-thumb {
  background: #009BF3;
  border-radius: 6px;
}

body {
  background: #d6dee6;
}

h1,
h2,
h3,
h4,
h5,
h6,
b,
strong {
  font-weight: 500;
}

.plugin-container {
